.ChatBox-container {
  background: var(--cardColor);
  border-radius: 1rem;
  display: grid;
  grid-template-rows: 14vh 60vh 13vh;
}

.chat-header {
  padding: 1rem 1rem 0rem 1rem;
  display: flex;
  flex-direction: column;
}

.chat-body {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 1.5rem;
  overflow: scroll;
}

.message {

background: var(--buttonBg);
color: white;
padding: 0.7rem;
border-radius: 1rem 1rem 1rem 0;
max-width: 28rem;
width: fit-content;
display: flex;
flex-direction: column;
background: linear-gradient(98.63deg, #ff7f50 0%, #ff6347 100%);
gap: 0.5rem;
}

.message-time {
  font-size: 0.7rem; /* Set the desired font size for the time */
  color: var(--textColor);
  align-self: end;
  margin: 4px; /* Add margin to create space between message and time */
}

.message-divider {
  font-size: 0.7rem; /* Set the desired font size for the divider */
  color: var(--textColor);
  margin: 0 4px; /* Add margin around the divider to create space */
}

.message>span:nth-child(2){
  font-size: 0.7rem;
  color: var(--textColor);
  align-self: end;
}

.own {
align-self: flex-end;
border-radius: 1rem 1rem 0 1rem;
background: linear-gradient(98.63deg, #24e4f0 0%, #358ff9 100%);
}

.other {
  align-self: flex-end;
  border-radius: 1rem 1rem 0 1rem;
  background: linear-gradient(98.63deg, #ff7f50 0%, #ff6347 100%); /* Different background colors */
}



.chat-sender {
  background: white;
  display: flex;
  justify-content: space-between;
  height: 3.5rem;
  align-items: center;
  gap: 1rem;
  padding: 0.8rem;
  border-radius: 1rem;
  align-self: end;
  width:100%;
}
.chat-sender>input{
  height: 70%;
  background-color: rgb(236, 236, 236);
  border-radius: 0.5rem;
  border: none;
  outline: none;
  flex  :1 ;
  font-size: 14px;
  padding: 0px 15px 0px 15px;
  
}
.chat-sender>div:nth-of-type(1)
{
  background: rgb(233, 233, 233);
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  cursor: pointer
}
.chat-sender>div{
  height: 70%;
  padding: 0px 15px 0px 15px;
}


.FollowersCard{
  width: 100%;
  border-radius: 0.7rem;
  gap: 1rem;
  display: flex;
  flex-direction: column;
  font-size: 13px;
  max-height: 10rem;
}
.follower{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.follower>div{
  display: flex;
  gap: 10px;
}


.followerImage{
  width: 3.2rem;
  height: 3.2rem;
  border-radius: 50%;
}

.name{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.name>span:nth-of-type(1){
  font-weight: bold;
}



.fc-button{
  height: 2rem;
  padding-left: 20px;
  padding-right: 20px;
}

.UnfollowButton{
  color: var(--orange);
  border: 2px solid var(--orange);
  cursor: pointer;
  background: transparent;
}


.FollowersCard>span{
  font-weight: bold;
  color: orange;
  align-self: center;
  cursor: pointer;
}

