/* Add styles for the send button */
.send-button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #0084ff; /* Facebook Messenger blue color */
  color: white;
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  cursor: pointer;
  font-size: 14px;
  margin-left: 10px;
  transition: background-color 0.3s ease;
}

.send-button:hover {
  background-color: #0072e3; /* Darker shade on hover */
}





